<template>
  <el-main style="padding-top:40px;">
    <h1 id="h_show">{{$t('resetPwd.title[0]')}}</h1>
    <p id="p_show">{{$t('resetPwd.title[1]')}}</p>
    <el-input
      :placeholder="pwd"
      type="password"
      v-model="newPwd"
      @blur="pwdVer"
      id="newPwd"
       v-show="pwdShow"
      clearable>
    </el-input>
    <el-input
      :placeholder="pwds"
      type="password"
      v-model="newPwds"
      @blur="pwdVers"
      id="newPwds"
      v-show="pwdsShow"
      clearable>
    </el-input>
    <el-button  :disabled="disabled" @click="send" v-loading="loading" v-show="btnShow">{{$t('resetPwd.value')}}</el-button>
    <el-row v-show="resetPwdMess">
      <el-col>{{this.$t('resetPwdMess[0]')}}</el-col>
    </el-row>
  </el-main>
</template>
<script>
import judege from "@/components/judegeMess/judegeMessage";
import UserManager from "../../axiosJoggle/UserManager";
import verfy from "@/regular/comRegular.js";
import md5 from "js-md5";
export default {
  data() {
    return {
      newPwd: "",
      newPwds: "",
      disabled: true,
      pwd: this.$t("resetPwd.placeholder[0]"),
      pwds: this.$t("resetPwd.placeholder[1]"),
      error: "",
      enError: "",
      loading: false,
      btnShow: true,
      pwdsShow: true,
      pwdShow: true,
      resetPwdMess: false,
      req_message: "",
      req_enMessage: ""
    };
  },
  methods: {
    // 第一次输入密码验证
    pwdVer() {
      let newPwdInp = document.getElementById("newPwd");
      verfy.pwdVerfy(pwd, this.pwd, this.$t("register[1]"));
    },
    // 重新输入密码验证
    pwdVers() {
      let newPwdsInp = document.getElementById("newPwds");
      if (this.newPwds) {
        if (this.newPwds === this.newPwd) {
          newPwdsInp.style.backgroundColor = "";
          this.disabled = false;
        } else {
          newPwdsInp.style.backgroundColor = "#FFC0CB";
          this.disabled = true;
          this.$message.error(this.$t("register[2]"));
        }
      }
    },
    //发送密码
    send() {
      this.loading = true;
      let _url = location.href;
      let _postdata = _url.split("?")[1];
      if (this.newPwd && this.newPwds) {
        UserManager.AgainForget({
          code: _postdata,
          password: md5(this.newPwds)
        })
          .then(req => {
            this.req_message = req.data.message;
            this.req_enMessage = req.data.enMessage;
            if (req.status == 200 && req.data.state == "success") {
              let p_show = document.getElementById("p_show");
              let h_show = document.getElementById("h_show");
              this.btnShow = false;
              this.pwdsShow = false;
              this.pwdShow = false;
              this.newPwd = "";
              this.newPwds = "";
              this.loading = false;
              p_show.innerHTML = "";
              h_show.innerHTML = "";
              this.resetPwdMess = true;
              judege.judegeSuccessMess(
                this.$store.state.locale,
                this.req_message,
                this.req_enMessage
              );
            } else {
              this.loading = false;
              judege.judegewarningMess(
                this.$store.state.locale,
                this.req_message,
                this.req_enMessage
              );
            }
          })
          .catch(error => {
            this.loading = false;
            judege.judegeErrorMess(
              this.$store.state.locale,
              this.req_message,
              this.req_enMessage
            );
          });
      }
    }
  }
};
</script>
<style lang="less" scoped>
h1 {
  font-size: 30px;
}
p {
  margin-top: 10px;
  color: rgb(144, 147, 153);
}
.el-input {
  width: 300px;
  display: block;
  margin: 20px auto;
}
</style>



