<template>
  <div class="reset" v-loading="loading">
        <h1 class="title">{{$t('account.title[2]')}}</h1>
        <el-input
          :placeholder="$t('account.lghint[2]')"
          v-model="mailbox"
          clearable
          autofocus
          @blur='mailVerify'
          id="mail"
        >
        </el-input>
        
        <div class="btn">
            <el-button id="reset" @click="reset">{{$t('account.btn[6]')}}</el-button>
        </div>
        <div class="help" >
            <router-link to="register" class="help_account">{{$t('account.btn[1]')}}</router-link>
            <router-link to="login" class="help_pwd">{{$t('account.btn[0]')}}</router-link>
        </div>
    </div>
</template>
<script>
import { mailReg } from "@/regular/regular";
import UserManager from "../../axiosJoggle/UserManager";
import judege from "@/components/judegeMess/judegeMessage";
import verfy from '@/regular/comRegular.js'
export default {
  data() {
    return {
      mailbox: "",
      req_message: "",
      req_enMessage: "",
      loading:false
    };
  },
  mounted(){
    console.log(location.href)
  },
  methods: {
    // 邮箱验证
    mailVerify(event) {
      let mail = document.getElementById("mail")
      verfy.mailVerfy(mail,this.mailbox,this.$t("register[0]"))
    },
    // 发送邮箱请求
    reset() {
      this.loading = true;
      UserManager.PwdForget({
        email: this.mailbox
      })
        .then(req => {
          this.req_message = req.data.message;
          this.req_enMessage = req.data.enMessage;
          this.loading = false;
          if (req.status == 200 && req.data.state == "success") {
            judege.judegeSuccessMess(
              this.$store.state.locale,
              this.req_message,
              this.req_enMessage,
            );
            this.mailbox = ""
          } else {
            judege.judegeWarningMess(
              this.$store.state.locale,
              this.req_message,
              this.req_enMessage,
            );
            this.loading = false;
          }
          // console.log(req)
        })
        .catch(error => {
          this.loading = false;
          judege.judegeErrorMess(
            this.$store.state.locale,
            this.req_message,
            this.req_enMessage,
          );
        });
    }
  }
};
</script>

<style>
.reset {
  width: 800px;
  height: 350px;
  border: 1px solid gray;
  box-shadow: 4px 2px 25px gray;
  margin: 0 auto;
  margin-top: 50px;
  position: relative;
}
.reset .title {
  font-size: 30px;
  margin-top: 20px;
}
</style>



