<template>
  <el-container class="account">
      <el-header>
          <com-nav/>
      </el-header>
      <el-main>
          <head-time/>
          <router-view></router-view>
      </el-main>
  </el-container>
</template>
<script>
import headTime from "../headTime.vue";
import comNav from "../comNav.vue";

export default {
  components: {
    headTime,
    comNav
  }
};
</script>

<style lang="less">
.account {
  .title {
    margin-top: 20px;
  }
  .help {
    position: absolute;
    bottom: 10px;
    right: 10px;
  }
  .el-input {
    width: 300px;
    height: 35px;
    display: block;
    margin: 0 auto;
    margin-top: 10px;
    font-size: 20px;
  }
  .btn{
      margin-top: 20px;
  }
}
</style>



