<template>
            <div class="register" v-loading="loading">
                <h3 class="title">{{$t('account.title[1]')}}</h3>
                <el-input
                :placeholder="$t('account.lghint[2]')"
                v-model="mailbox"
                clearable
                @blur='mailVerify'
                id="mail"
                >
                </el-input>
                <el-input
                :placeholder="$t('account.lghint[1]')"
                v-model="pwd"
                clearable
                type="password"
                @blur='pwdVerify'
                id="pwd"
                >
                </el-input>
                <el-input
                :placeholder="$t('account.lghint[3]')"
                v-model="pwds"
                clearable
                type="password"
                id="pwds"
                @blur='pwdVerifys'
                >
                </el-input>
                <div style="margin-top:20px">
                    <el-radio v-model="radio" label="1">{{$t('account.btn[4]')}}
                        <router-link to="">{{$t('account.btn[5]')}}</router-link>
                    </el-radio>
                </div>
                <div class="btn">
                    <el-button @click="send" id="btn">
                        <i class="fa fa-user"></i>
                        {{$t('account.btn[3]')}}
                    </el-button>
                </div>
                <div class="help">
                    <router-link to="forget" class="help_account">{{$t('account.btn[2]')}}</router-link>
                    <router-link to="login" class="help_pwd">{{$t('account.btn[0]')}}</router-link>
                </div>
            </div>
</template>
<script>
import headTime from "../headTime.vue";
import comNav from "../comNav.vue";
import verfy from '@/regular/comRegular.js'
import UserManager from "../../axiosJoggle/UserManager";
import judege from "@/components/judegeMess/judegeMessage";
import md5 from "js-md5";
export default {
  data() {
    return {
      input3:"123",

      mailbox: "",
      pwd: "",
      pwds: "",
      mailLock: false,
      radio: "0",
      loading: false,
      locale: localStorage.getItem("locale"),
      req_message: "",
      req_enMessage: ""
    };
  },
  mounted() {
  },
  methods: {
    // 邮箱验证
    mailVerify() {
      let mail = document.getElementById("mail")
      verfy.mailVerfy(mail,this.mailbox,this.$t("register[0]"))
    },
    // 密码验证
    pwdVerify() {
      let pwd = document.getElementById("pwd")
      verfy.pwdVerfy(pwd,this.pwd,this.$t("register[1]"))
    },
    // 再次输入密码验证
    pwdVerifys() {
      if (this.pwd == this.pwds) {
        document.getElementById("pwds").style.backgroundColor = "";
      } else {
        document.getElementById("pwds").style.backgroundColor = "#FFC0CB";
        this.$message.error(this.$t("register[2]"));
      }
    },
    // 发送请求
    send() {
      this.loading = true;
      if (
        this.mailLock == true &&
        this.pwdLock == true &&
        this.radio == "1" &&
        this.pwdLocks == true
      ) {
        UserManager.UsersRegister({
          email: this.mailbox,
          password: md5(this.pwds)
        })
          .then(req => {
            this.req_message = req.data.message;
            this.req_enMessage = req.data.enMessage;
            if (req.status == 200) {
              if (req.data.state == "success") {
                judege.judegeSuccessMess(
                  this.$store.state.locale,
                  this.req_message,
                  this.req_enMessage
                );
                localStorage.setItem("login", "ok");
                localStorage.setItem("email", req.data.data.email);
                localStorage.setItem("mobilePhone", req.data.data.mobilePhone);
                this.$router.push({ name: "systemStts" });
                this.mailLock = false;
                this.pwdLock = false;
                this.pwdLocks = false;
              } else if (req.data.state == "error") {
                judege.judegeWarningMess(
                  this.$store.state.locale,
                  this.req_message,
                  this.req_enMessage
                );
                this.mailbox = "";
              }
            }
            this.loading = false;
          })
          .catch(error => {
            judege.judegeErrorMess(
              this.$store.state.locale,
              this.req_message,
              this.req_enMessage
            );
            this.loading = false;
          });
      } else if (this.radio != "1") {
        this.$message.warning(this.$t("register[5]"));
        this.loading = false;
      }
    }
  },
  components: {
    headTime,
    comNav
  }
};
</script>
<style lang="less" scoped>
    @media screen and(max-width:578.95){
      
    }
    .register {
      width: 800px;
      height: 350px;
      border: 1px solid gray;
      box-shadow: 4px 2px 25px gray;
      margin: 0 auto;
      margin-top: 50px;
      position: relative;
    }
    .register .title {
      font-size: 30px;
    }
</style>






